:host .tab-bar {
  box-sizing: border-box;
  position: relative;
  background-color: var(--mdx-comp-tabs-background-color-default);

  &__tabs {
    display: flex;
    flex-direction: row;
    width: 100%;
    overflow-x: hidden;
    border-bottom-style: solid;
    background-color: var(--mdx-comp-tabs-background-color-default);
    border-bottom-width: var(--mdx-comp-tabs-border-width-bottom-default);
    border-top-width: var(--mdx-comp-tabs-border-width-top-default);
    border-left-width: var(--mdx-comp-tabs-border-width-left-default);
    border-right-width: var(--mdx-comp-tabs-border-width-right-default);
    border-color: var(--mdx-comp-tabs-border-color-default);
  }

  &__chevron {
    z-index: 20;
    position: absolute;
    height: 100%;
    cursor: pointer;
    visibility: hidden;
    color: var(--mdx-comp-tabs-scroll-element-icon-color-default);
    &:hover {
      color: var(--mdx-comp-tabs-scroll-element-icon-color-hover);
    }

    &__left {
      left: 0;
      padding-left: var(--mdx-comp-tabs-padding-scroll-element-left-left-desktop);
      padding-right: var(--mdx-comp-tabs-padding-scroll-element-left-right-desktop);
      background-image: linear-gradient(
        to left,
        rgba(255, 255, 255, 0) 0%,
        var(--mdx-comp-tabs-background-color-default) var(--mdx-comp-tabs-sizing-scroll-element-gradient-desktop),
        var(--mdx-comp-tabs-background-color-default) 100%
      );

      @media only screen and (max-width: 768px) {
        padding-left: var(--mdx-comp-tabs-padding-scroll-element-left-left-mobile);
        padding-right: var(--mdx-comp-tabs-padding-scroll-element-left-right-mobile);
        background-image: linear-gradient(
          to left,
          rgba(255, 255, 255, 0) 0%,
          var(--mdx-comp-tabs-background-color-default) var(--mdx-comp-tabs-sizing-scroll-element-gradient-mobile),
          var(--mdx-comp-tabs-background-color-default) 100%
        );
      }
    }

    &__right {
      right: 0;
      top: 0;
      padding-left: var(--mdx-comp-tabs-padding-scroll-element-right-left-desktop);
      padding-right: var(--mdx-comp-tabs-padding-scroll-element-right-right-desktop);
      background-image: linear-gradient(
        to right,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 1) var(--mdx-comp-tabs-sizing-scroll-element-gradient-desktop),
        rgba(255, 255, 255, 1) 100%
      );

      @media only screen and (max-width: 768px) {
        padding-left: var(--mdx-comp-tabs-padding-scroll-element-left-left-mobile);
        padding-right: var(--mdx-comp-tabs-padding-scroll-element-left-right-mobile);
        background-image: linear-gradient(
          to right,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 1) var(--mdx-comp-tabs-sizing-scroll-element-gradient-mobile),
          rgba(255, 255, 255, 1) 100%
        );
      }
    }
  }
}

:host([fill-width]) {
  ::slotted(*) {
    flex-grow: 1;
    overflow-x: hidden;
    width: 100%;
  }
  .tab-bar__tabs {
    justify-content: space-evenly;
  }
}
